// ** React Imports
import { Fragment, useState } from 'react'
// ** Third Party Components
import { useSelector, useDispatch } from 'react-redux'
import { Row, Col, Card, CardHeader, CardTitle, CardBody, Badge, Button } from 'reactstrap'
import { buyPackage } from '../store/actions'

import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core'
import { parseUnits, formatUnits } from '@ethersproject/units'
import { toast } from 'react-toastify'
import Web3 from 'web3'

const CardBusiness = (props) => {
  const { dataArr, packageTitle, price } = props
  const dispatch = useDispatch()
  const userData = useSelector(state => state.auth.userData._id)
  const MySwal = withReactContent(Swal)


  const { account, activate, library, active } = useWeb3React()
  const [depositMAmount, setDepositMAmount] = useState('10')

  const onDepositMetamask = async () => {
    console.log('onDepositMetamask')
    if (!library.provider) {
      return toast.warning('Please connect your wallet')
    } else {
      const web3 = new Web3(library.provider)
      const decimals = 18
      const amounti = parseUnits(depositMAmount, decimals)
      const balance = await web3.eth.getBalance(account)
      console.log(balance)
      if (formatUnits(balance, decimals) < depositMAmount) {
        return toast.error('Balance not enough.')
      }
      web3.eth.sendTransaction({ from: account, to: '0x6BB7cbb2965C108db26edC911045Fe27Ab599104', value: amounti })
        .on('confirmation', (data, receipt) => {
          if (receipt.status) {
            return depositMetamask(receipt, amounti)
          }
        })
    }
  }

  const buyPackage_int = async (title) => {
    if (active) {
      return MySwal.fire({
        title: 'Are you sure?',
        text: "Do you buy this package?",
        icon: 'info',
        showCancelButton: true,
        confirmButtonText: 'Yes, buy this!',
        customClass: {
          confirmButton: 'btn btn-info',
          cancelButton: 'btn btn-outline-danger ml-1'
        },
        buttonsStyling: false
      }).then(async (result) => {
        if (result.value) {
          const data = await onDepositMetamask()
          dispatch(buyPackage(title, userData))
        }
      })
    } else {
      toast.info("First, Connect your wallet!")
    }
  }
  const renderOptions = () => {
    return dataArr.map(item => {
      // console.log(item)
      return (
        <div key={item._id} className='business-item m-0'>
          <div className='d-flex align-items-center justify-content-between'>
            <span>{item.title}</span>
            <Badge color={'light-success'}>{item.userNumber}</Badge>
            <Badge color={'light-danger'}>{`${item.tokenType} ${item.budget}`}</Badge>
          </div>
        </div>
      )
    })
  }

  return (
    <Card className='business-card'>
      <CardHeader className='pb-1'>
        <CardTitle tag='h4'>{packageTitle}</CardTitle>
        <h6 className='item-price mt-1' style={{ color: 'lime' }}>{`$ ${price}`}</h6>
        {/* <MoreVertical size={18} className='cursor-pointer' /> */}
      </CardHeader>
      <CardBody className="d-flex flex-column justify-content-between">
        <div className='business-items'>{renderOptions()}</div>
        <Button color='success' onClick={() => buyPackage_int(packageTitle)} outline block >
          B U Y
        </Button>
      </CardBody>
    </Card>
  )
}

const ProductCards = props => {
  // ** Props
  const { store } = props

  const renderPackages = () => {
    return store.packagesWithTask.map(item => {
      return (
        <Col lg='4' md='6' sm='12' key={item.packageTitle}>
          <CardBusiness dataArr={item.data} packageTitle={item.packageTitle} price={item.price} />
        </Col>
      )
    })
  }
  return (

    <Fragment>
      <Row className='match-height mt-2'>
        {renderPackages()}
      </Row>
    </Fragment>
  )
}

export default ProductCards
